import { InsertionContext, InsertionTraverse, ProgramNode } from '@universal-robots/contribution-api';
import { getAsyncIterableIteratorStub } from './async-iterable-iterator-stub';

/**
 * Testing utility for serialization of an InsertionContext.
 */
export class InsertionContextForWorkerSerialization implements InsertionContext {
    type: string;
    traverse: InsertionTraverse;
    ancestors: Array<Readonly<ProgramNode>> | undefined;
    nextSiblings: Array<Readonly<ProgramNode>> | undefined;
    previousSiblings: Array<Readonly<ProgramNode>> | undefined;

    /**
     * Initializes a new instance of the InsertionContextForWorkerSerialization class.
     */
    constructor() {
        this.type = '$$ScriptContext';
        this.traverse = {
            ancestors: getAsyncIterableIteratorStub(),
            nextSiblings: getAsyncIterableIteratorStub(),
            previousSiblings: getAsyncIterableIteratorStub(),
        };
    }
}
