import { ProgramNode, ScriptContext, TreeTraverse } from '@universal-robots/contribution-api';
import { getAsyncIterableIteratorStub } from './async-iterable-iterator-stub';

/**
 * Testing utility for serialization of a ScriptContext.
 */
export class ScriptContextForWorkerSerialization implements ScriptContext {
    type: string;
    traverse: TreeTraverse;
    ancestors: Array<Readonly<ProgramNode>> | undefined;
    children: Array<Readonly<ProgramNode>> | undefined;
    depthFirst: Array<Readonly<ProgramNode>> | undefined;
    nextSiblings: Array<Readonly<ProgramNode>> | undefined;
    previousSiblings: Array<Readonly<ProgramNode>> | undefined;

    /**
     * Initializes a new instance of the ScriptContextForWorkerSerialization class.
     */
    constructor() {
        this.type = '$$ScriptContext';
        this.traverse = {
            ancestors: getAsyncIterableIteratorStub(),
            children: getAsyncIterableIteratorStub(),
            depthFirst: getAsyncIterableIteratorStub(),
            nextSiblings: getAsyncIterableIteratorStub(),
            previousSiblings: getAsyncIterableIteratorStub(),
        };
    }
}
